import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';

import { FileService } from './file.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {

  @ViewChild('myCanvas') canvasRef: ElementRef;

  cookieName = 'la';

  expires: Date;

  countDown: string;

  constructor(private cookieService: CookieService, fileService: FileService) {
    fileService.getFileData('assets/data.json').subscribe(data => {
      console.log(data);
      if (data && data.hasOwnProperty('list')) {
        console.log(data.list);
      }
    });
  }

  printAllCookies() {
    const allCookies = this.cookieService.getAll();
    console.log(allCookies);
    console.log(document.cookie);
  }

  exists(): boolean {
    return this.cookieService.check(this.cookieName);
  }

  addCookie(add: boolean) {
    let data = 0;
    if (this.exists()) {
      const dataNum = Number(this.cookieService.get(this.cookieName));
      if (dataNum || dataNum === 0) {
        data = dataNum + (add ? 1 : -1);
      }
    }
    this.expires = this.addMin();
    this.cookieService.set(this.cookieName, data.toString(), this.expires);
    this.printAllCookies();
  }

  clearCookie() {
    this.cookieService.delete(this.cookieName);
    delete this.expires;
    this.printAllCookies();
  }

  getCookie(): string {
    const cookie = this.cookieService.get(this.cookieName);
    return cookie ? 'Value: ' + cookie : '';
  }

  addMin(date?: Date): Date {
    if (!date) {
      date = new Date();
    }
    date.setMinutes(date.getMinutes() + 1);
    return date;
  }

  getCountDown(): string {
    if (this.expires) {
      const now = new Date();
      const diff = this.expires.getTime() - now.getTime();
      if (diff < 0 || !this.getCookie()) {
        delete this.expires;
        return;
      }
      return (diff / 1000).toString().split('.')[0];
    }
  }

  assignCountdown() {
    setTimeout(() => {
      const countDown = this.getCountDown();
      this.countDown = countDown ? 'Expires in ' + countDown + 'sec' : '';
      this.assignCountdown();
    });
  }

  ngOnInit() {
    this.printAllCookies();
    this.assignCountdown();

    const ctx  = this.canvasRef.nativeElement.getContext('2d');
    this.paint(ctx, 0);
  }

  paint(ctx, x: number) {
    // ctx.drawImage(this.spaceshipAlly.nativeElement, 0, 0,50,50);
    ctx.beginPath();
    ctx.moveTo(125 + x, 30);
    ctx.lineTo(31.9, 63.2);
    ctx.lineTo(46.1, 186.3);
    ctx.closePath();
    ctx.stroke();
    // ctx.fill();

    setTimeout(() => {
      this.paint(ctx, x < 300 ? x + 1 : 0 );
    }, 300);
  }

}
